.square {
  height: 5rem;
  width: 5rem;
  margin: 1rem 0.5rem;
  background-color: #EEEEEE;
}

.square-row:first-child .square {
  margin: 1rem;
}

.square:hover {
  background-color: #DDDDDD;
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
}

.square > .player1, .square > .player2 {
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  margin: 1rem;
}

.square > .pending {
  opacity: 0.3;
}

.square > .player1 {
  background-color: blue;
}

.square > .player2 {
  background-color: red;
}

.font-white {
  color: green;
}


